*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: black;
    
}

.card-text{
    font-family: fantasy;
    color:white;
}


h5 {
    
    text-shadow: 2px 2px 5px white;
    font-weight: bolder;
      
}


.live{
    height: 100px;
    color: white;
}

h2{
    width: 100%;
    padding-top: 30px;
    padding-bottom: 30px;
    display: flex;
    justify-content: center;
    margin: top 30px; ;
    margin-bottom: 30px;
}

 h2{
    text-transform: uppercase;
    font-size: 1.6rem;
    animation: aagepiche 0.8s linear infinite;
    animation-direction: alternate;
}
@keyframes aagepiche{
    from{ font-size: 1.6rem;}
    to { font-size: 2rem;}
}




h1{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: bolder;
}
.card{
    margin-top: 40px ;
    margin-bottom: 30px;
    background-color: yellowgreen;
    margin-left: 20px;
    margin-right: 20px;
    height: 200px;
   width: 30%;
   float: left;
}
.card-body p,h5{
    display: flex;
    justify-content: center;
    display: flex;
    padding: 3px;

}
.card-body p{
    font-size: 1.5rem;
    align-items: center;
    text-align: center;
}
.card-body h5{
    font-size: 2.3rem;
    align-items: center;
    text-align: center;
}


@media only screen and (max-width: 800px) {
    .card {
      width: 90%;

    }
  }